/*-------------------------------------------------------------------/*
Template name : PerStar
Version    : 1.0.0
Author     : xdesky
Last change: 2018/05/06
/*-------------------------------------------------------------------
[Color codes]
    Background:   #030408
    Body Color :  #666666
    Accent color: #030408
[Typography]
    Body:           14px Roboto, sans-serif;
    Heading:        Poppins, sans-serif;
    Small Heading:  Playfair Display, serif;

[Table of contents]

    1. COMMON STYLES
    2. LEFT SIDEBAR
    3. VCARD
    4. CONTENT
        4.1. Common
    	4.2. Home
    	4.3. About
    	4.4. Resume
    	4.5. Skill
    	4.6. Portfolio
    	4.7. Contact
    5. RESPONSIVE

-------------------------------------------------------------------*/

/*-------------------
1. COMMON STYLES
---------------------*/
// Variables
@background: #030408;
@body_color: #666666;
@accent_color: #030408;
@text_color: #ffffff;
@title_color: #201f24;
@sub_title: #999999;

//layout width
@sidebar_w: 80px;
@vcard_w: 350px;
@content_w: @sidebar_w + @vcard_w;
@avatar_w: 168px;
@regtangle_w: 580px;

//common
body{
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    line-height: 24px;
    color: #333;
    background-color: @background;
    font-weight: 500;
    -webkit-font-smoothing: antialiased;
}
img{
    max-width: 100%;
}
h1,h2,h3,h4,h5,h6{
    line-height: 1.3em;
    margin-top: 0;
    color: #222;
}
h1{
    font-size: 36px;
}
h2{
    font-size: 30px;
}
h3{
    font-size: 24px;
}
h4{
    font-size: 20px;
}
h5{
    font-size: 16px;
}
h6{
    font-size: 14px;
}
button:focus{
    outline: none;
}
a{
    color: @text_color;
    text-decoration: none;
    .transition;
}
.transition{
    -webkit-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
}
.rotate (@deg) {
    -ms-transform: rotate(@deg); /* IE 9 */
    -webkit-transform: rotate(@deg); /* Safari */
    transform: rotate(@deg);
}
a:hover, a:focus{
    text-decoration: none;
    color: inherit;
    outline: none;
}
p{
    margin-bottom: 22px;
}
ul, ol{
    margin: 0;
    padding: 0;
}
ul ul, ol ol, ul ol, ol ul{
    padding-left: 30px
}
.wrap-text{
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 300px;
    color: @body_color;
    a{
        color: @body_color;
    }
}
.pre-header{
    font-family: 'Playfair Display', serif;
    font-size: 20px;
}
.line{
    width: 40px;
    height: 5px;
    background-color: #fff;
    display: block;
    margin-bottom: 30px;
}
.mt20{
    margin-top: 20px;
}
.mt30{
    margin-top: 30px;
}
.grey{
    background-color: #ccc;
}
::-webkit-input-placeholder{
    color: inherit;
    opacity: 1;
    filter: alpha(opacity=100);
}
:-moz-placeholder{ /* Firefox 18- */
    color: inherit;
    opacity: 1;
    filter: alpha(opacity=100);
}
::-moz-placeholder{  /* Firefox 19+ */
    color: inherit;
    opacity: 1;
    filter: alpha(opacity=100);
}
:-ms-input-placeholder{
    color: inherit;
    opacity: 1;
    filter: alpha(opacity=100);
}
::-webkit-scrollbar {
    width: 10px;
    background-color: #F5F5F5;
}
::-webkit-scrollbar:horizontal {
    height: 10px;
}
::-webkit-scrollbar-thumb {
    background-color: #030408;
}
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 10px rgba(0,0,0,0.5);
    background-color: #F5F5F5;
}
.body-overlay{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    background-color: rgba(0,0,0, .75);
    opacity: 0;
    visibility: hidden;
    transition: .25s all ease-in-out;
    &.open{
        opacity: 1;
        visibility: visible;
    }
}
.list-social{
    list-style: none;
    width: 100%;
    li{
        display: inline-block;
        width: 35px;
        height: 35px;
        line-height: 37px;
        text-align: center;
        border: 2px solid #cecece;
        margin-right: 5px;
        opacity: 0.7;
        transition: all .25s ease-in-out;
        cursor: pointer;
        a{
            font-size: 0px;
            color: #cecece;
        }
        .ps{
            font-size: 18px;
        }
        &:hover{
            border-color: #030408;
            opacity: 1;
            a{
                color: #030408;
            }
        }
    }
}
#particles-js {
    position: absolute;
    width: 100%;
    height: 100%;
    background: transparent;
    z-index: 1;
}
/*-------------------
2. LEFT SIDEBAR
---------------------*/
.sidebar-left{
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    padding-top: 80px;
    background-color: @background;
    z-index: 9;
    transition: all .5s ease-in-out;
    &.on{
        left: 0;
        width: 100%;
    }
    .nav{
        display: block;
        flex-wrap: initial;
        height: 100%;
        overflow: hidden;
        overflow-y: auto;
        li{
            display: table;
            float: left;
            width: 50%;
            height: @sidebar_w;
            border-bottom: 1px solid rgba(255,255,255,0.2);
            a{
                display: table-cell;
                vertical-align: middle;
                height: 100%;
                text-transform: uppercase;
                font-size: 11px;
                text-align: center;
                .ps{
                    font-size: 18px;
                }
                span{
                    display: block;
                }
                &.active{
                    background-color: rgba(255,255,255,0.1);
                }
                &:hover{
                    background-color: rgba(255,255,255,0.1);
                    color: #fff;
                }
            }
            &:first-child{
                span{
                    display: none;
                }
            }
        }
    }
}
/*-------------------
3. VCARD
---------------------*/
.vcard-box{
    width: @vcard_w;
    height: 100%;
    overflow-y: auto;
    position: fixed;
    z-index: 8;
    top: 0px;
    padding: 0px 30px;
    left: @sidebar_w;
    background: url("../images/topography.svg") #fff repeat top left;
    .avatar-section{
        width: @avatar_w;
        height: @avatar_w;
        margin: auto;
        margin-top: 50px;
        margin-bottom: 50px;
        img{
            width: 100%;
        }
    }
    .infor-section{
        width: 100%;
        text-align: center;
        .name-title{
            font-size: 40px;
            color: @title_color;
            font-weight: 700;
        }
        .sub-title{
            font-size: 28px;
            color: @sub_title;
        }
        .description{
            margin-top: 50px;
            text-align: justify;
            -moz-text-align-last: center;
            text-align-last: center;
            display: block;
        }
        .btn-downloadcv{
            background-color: @title_color;
            display: inline-block;
            padding: 10px 20px;
            text-align: center;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 12px;
            margin-top: 50px;
            .transition;
            .ps{
                font-size: 16px;
                margin-right: 7px;
                vertical-align: -2px;
            }
            &:hover{
                opacity: .8;
                .transition;
            }
            a:hover{
                color: #fff;
            }
        }
    }
    .footer-section{
        border-top: 1px dotted #b9b9b9;
        text-align: center;
        display: block;
        margin-top: 50px;
        p{
            margin-top: 30px;
            color: @body_color;
        }
    }
}
/*-------------------
4. CONTENT
---------------------*/
/*
4.1. Common
*/
.content-wrap{
    position: relative;
    background-color: @background;
    .background-video{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
    }
    .content-flex{
        position: relative;
        height: 100vh;
        .regtangle{
            position: absolute;
            top: 50%;
            left: 50%;
            width: 100%;
            height: calc(~"0px + "@regtangle_w*2/5);
            transform: translate(-50%, -50%);
            z-index: 4;
            display: block;
            border: 15px solid rgba(255,255,255, 0.2);
        }
    }
    .c-common{
        position: relative;
        padding: 0 30px 50px;
        &:not(.sub-page){
            position: absolute;
            top: 0;
            width: 100%;
            height: 100vh;
            padding: 0;
            .c-common-text{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                padding: 0 30px;
                z-index: 4;
                h1{
                    font-size: 60px;
                }
            }
        }
        .c-common-text{
            h1{
                font-weight: 700;
                color: #fff;
                text-transform: uppercase;
            }
            h2{
                color: #fff;
                font-size: 18px;
            }
            .c-common-des{
                color: #fff;
                font-size: 15px;
            }
            .action-btn{
                display: block;
                margin-top: 20px;
                a:hover {
                    opacity: 0.4;
                    color: #fff;
                }
                .preview{
                    width: 30px;
                    display: inline-block;
                    margin-right: 10px;
                }
                .next{
                    width: 30px;
                    display: inline-block;
                }
            }
        }
    }
    .text-free{
        font-size: 70px;
        letter-spacing: 5px;
        font-weight: 700;
        opacity: 0.1;
        text-transform: uppercase;
        position: fixed;
        width: 800px;
        height: 0px;
        line-height: 0px;
        z-index: 2;
        color: #fff;
        bottom: -25px;
        right: -750px;
        .rotate(-90deg);
        -webkit-transform-origin: left top;
        -ms-transform-origin: left top;
        -moz-transform-origin: left top;
        -o-transform-origin: left top;
        transform-origin: left top;
        text-align: center;
    }
    .background{
        position: fixed;
        top: 0;
        left: 0;
        z-index: 0;
        width: 100vw;
        height: 100vh;
        background-size: cover;
        background-position: top center;
        background-repeat: no-repeat;
        &::after{
            content: '';
            position: absolute;
            top: 0px;
            left: 0px;
            width: 100vw;
            height: 100vh;
            background-color: rgba(0,0,0, 0.6);
        }
    }
}
/*
4.3. About
*/
.sub-page h1{
    font-size: 30px !important;
}
.title-pro{
    position: absolute;
    font-size: 50px;
    font-weight: 700;
    color: #fff;
    width: 100%;
    text-align: center;
    top: 50%;
    transform: translateY(-50%);
    &::after{
        content: "";
        display: block;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: 50px;
        height: 5px;
        margin-top: 5px;
        background-color: #fff;
    }
}
.list-count{
    width: 100%;
    list-style: none;
    padding: 0 30px 50px;
    z-index: 5;
    text-align: center;
    li{
        float: left;
        width: 50%;
        padding: 10px 0 10px 20px;
        position: relative;
        text-align: left;
        opacity: .5;
        .transition;
        h4{
            position: relative;
            color: #fff;
            font-size: 40px;
            font-weight: 700;
            margin-bottom: 0px;
            &::before{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: -20px;
                content: "";
                width: 10px;
                height: 10px;
                background-color: #fff;
                border-radius: 10px;
            }
        }
        span{
            color: #fff;
            opacity: 0.6;
        }
        &:hover{
            opacity: 1;
            .transition;
        }
    }
}
/*
4.4. Resume
*/
.resume-contents{
    .content-modal{
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0px;
        right: 0px;
        z-index: 5;
        background-color: @background;
        overflow-x: hidden;
        overflow-y: auto;
        display: none;
        .closer-resume{
            display: block;
            position: absolute;
            top: 10px;
            right: 10px;
            z-index: 9;
            width: 30px;
            height: 30px;
            line-height: 30px;
            text-align: center;
            color: #fff;
            opacity: 0.5;
            cursor: pointer;
            .transition();
            &:hover{
                .rotate(45deg);
                opacity: 1;
            }
        }
        .resume-modal{
            width: 100%;
        }
        .owl-nav{
            width: 100px;
            float: left;
            text-align: right;
            position: absolute;
            height: 50px;
            top: 70px;
            right: 60px;
            button{
                width: 30px;
                height: 30px;
                line-height: 30px;
                display: inline-block;
                margin-top: 18px;
                color: #fff;
                font-size: 0;
                text-align: center;
                cursor: pointer;
                opacity: .5;
                .transition;
                &:hover{
                    opacity: 1;
                    .transition;
                }
                &::before{
                    content: "";
                    font-family: 'perstar' !important;
                    speak: none;
                    font-size: 16px;
                    font-style: normal;
                    font-weight: normal;
                    font-variant: normal;
                    text-transform: none;
                    line-height: 1;
                    -webkit-font-smoothing: antialiased;
                    -moz-osx-font-smoothing: grayscale;
                }
            }
            .disabled{
                opacity: .1;
            }
            .owl-prev::before{
                content: "\e90c";
            }
            .owl-next::before{
                content: "\e911";
            }
        }
        .secondary:hover,.secondary:focus,.secondary:hover,.secondary:focus {
            color: #fff;
            opacity: 1;
        }

        .cm-wraper{
            width: 100%;
            display: block;
            padding: 65px 30px 0 30px;
            z-index: 10;
            position: relative;
            float: left;
            .content-modal-header{
                width: 100%;
                height: 100px;
                margin-top: 20px;
                display: block;
                float: left;
                .cmh-left{
                    width: 100%;
                    float: left;
                    padding-left: 50px;
                    color: #fff;
                    .cmhl-sub-title{
                        display: block;
                        position: relative;
                        font-family: 'Playfair Display', serif;
                        font-size: 18px;
                        opacity: .5;
                        width: calc(~"100% - 90px");
                        &::before{
                            content: "";
                            width: 40px;
                            height: 4px;
                            background-color: #fff;
                            position: absolute;
                            left: -50px;
                            top: 15px;
                        }
                    }
                    .cmhl-main-title{
                        display: block;
                        font-size: 25px;
                        font-weight: 700;
                        margin-top: 15px;
                        text-transform: uppercase;
                    }
                }
                .cmh-btn-slider{
                    float: left;
                    width: 100px;
                    text-align: right;
                    .prev{
                        width: 30px;
                        height: 30px;
                        display: inline-block;
                        background: url('../images/icons/left-arrow.svg') center center no-repeat;
                        background-size: 50%;
                        font-size: 0px;
                        margin-top: 18px;
                        cursor: pointer;
                        opacity: .3;
                    }
                    .next{
                        width: 30px;
                        height: 30px;
                        display: inline-block;
                        background: url('../images/icons/right-arrow.svg') center center no-repeat;
                        background-size: 50%;
                        font-size: 0px;
                        margin-top: 18px;
                        cursor: pointer;
                        opacity: .3;
                    }
                }
                .cmh-textbg{
                    width: 100%;
                    font-size: 45px;
                    font-weight: 700;
                    color: #fff;
                    opacity: 0.06;
                    position: fixed;
                    top: 92px;
                    right: 0;
                    text-transform: uppercase;
                    text-align: right;
                }
            }
            .content-modal-middle{
                width: 100%;
                padding-top: 30px;
                display: block;
                float: left;
                .cmm-image{
                    float: left;
                    position: relative;
                    width: 220px;
                    height: 283px;
                    margin-bottom: 80px;
                    img{
                        width: 100%;
                        z-index: 11;
                        position: absolute;
                    }
                    .border-img{
                        width: 220px;
                        height: 283px;
                        position: absolute;
                        top: 50px;
                        right: -50px;
                        border: 10px solid #fff;
                        opacity: .1;
                    }
                }
                .cmm-contents{
                    float: left;
                    color: #fff;
                    padding-bottom: 50px;
                    font-size: 16px;
                    .link-web{
                        display: block;
                        opacity: .5;
                        &::before{
                            content: "\e915";
                            font-family: 'perstar' !important;
                            speak: none;
                            font-style: normal;
                            font-weight: normal;
                            font-variant: normal;
                            text-transform: none;
                            line-height: 1;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                            margin-right: 10px;
                            vertical-align: middle;
                        }
                    }
                    .wrap-text{
                        color: #fff;
                        border-left: 3px solid rgba(255,255,255, 0.3);
                        padding-left: 15px;
                        font-size: 16px;
                    }
                    ul{
                        list-style: none;
                        margin-left: 15px;
                        columns: 2;
                        -webkit-columns: 2;
                        -moz-columns: 2;
                        opacity: .7;
                        li{
                            line-height: 34px;
                        }
                    }
                }
                .cmm-pretext{
                    font-size: 200px;
                    font-weight: 700;
                    color: #fff;
                    position: absolute;
                    right: 50px;
                    bottom: 180px;
                    opacity: .1;
                    &:before{
                        content: "0";
                        font-size: 200px;
                        font-weight: 700;
                        color: #fff;
                    }
                }
            }
            .content-modal-footer{
                width: 100%;
                height: 110px;
                display: block;
                float: left;
                margin-bottom: 30px;
                .years-resume{
                    li{
                        opacity: .3;
                        &.active{
                            opacity: 1;
                        }
                        &:hover{
                            opacity: 1;
                        }
                    }
                }
            }
        }
        .cm-overlay{
            background-color: rgba(0,0,0, .75);
            width: 100%;
            height: 100%;
            z-index: 4;
            position: absolute;
        }
        .cm-background{
            width: 100%;
            height: 100%;
            z-index: 2;
            position: absolute;
            background-position: center;
            background-size: cover;
        }
    }
}
/*
4.5. Skill
*/
.skills-container{
    position: relative;
    padding: 0 30px 50px;
    .bar {
        width: 100%;
        margin: 4px 0px;
        float: left;
        .skill {
            font-size: .8em;
            text-transform: uppercase;
            margin-bottom: 0.25em;
            color: #fff;
            float: left;
            width: 30%;
        }
        .bar-outer {
            height: 5px;
            background: rgba(255,255,255,0.3);
            position: relative;
            border-radius: 0.3em;
            width: 70%;
            float: right;
            margin-top: 8px;
            .bar-inner {
                width: 0;
                height: 5px;
                border-radius: 0.3em;
            }
            .speech-bubble {
                font-size: 0.75em;
                line-height: 1.3em;
                position: absolute;
                top: -1.8em;
                text-align: center;
                min-width: 2.5em;
                border-radius: 0.3em;
                display: none;
            }
            .speech-bubble:after {
                border: 0.5em solid transparent;
                content: "";
                margin-left: -0.5em;
                position: absolute;
                top: 100%;
                left: 50%;
            }
            .html, .photo, .fluent {
                left: calc(75% - 1.5em);
            }
            .jquery {
                left: calc(25% - 1.5em);
            }
            .sketch, .illustrator {
                left: calc(90% - 1.5em);
            }
            .indesign {
                left: calc(85% - 1.5em);
            }
            .ht, .html {
                background: #fff;
            }
            .html:after {
                border-top-color: #fff;
            }
            .jq, .jquery {
                background: #fff;
            }
            .jquery:after {
                border-top-color: #fff;
            }
            .sk, .sketch {
                background: #fff;
            }
            .sketch:after {
                border-top-color: #fff;
            }
            .ph, .photo {
                background: #fff;
            }
            .photo:after {
                border-top-color: #fff;
            }
            .il, .illustrator {
                background: #fff;
            }
            .illustrator:after {
                border-top-color: #fff;
            }
        }
    }
    .skill-language{
        width: 100%;
        float: left;
        margin-top: 30px;
        text-align: right;
        span{
            text-transform: uppercase;
            font-size: 13px;
            color: #fff;
            font-weight: 700;
        }
        ul{
            list-style: none;
            li{
                color: #fff;
                display: inline-block;
                margin-right: 10px;
                &:before{
                    content: "";
                    width: 8px;
                    height: 8px;
                    border-radius: 8px;
                    background-color: #e8e000;
                    float: left;
                    margin-top: 10px;
                    margin-right: 5px;
                }
                &:last-child{
                    margin-right: 0px;
                    &:before{
                        background-color: #7adfdb;
                    }
                }
            }
        }
    }
}
/*
4.6. Portfolio
*/
.portfolio-list{
    position: relative;
    padding: 0 30px 50px;
    color: #fff;
    z-index: 3;
    .portfolio-ca{
        div{
            text-align: center;
            a{
                display: inline-block;
                width: 80px;
                height: 80px;
                line-height: 85px;
                text-align: center;
                background-color: rgba(255,255,255,0.1);
                border-radius: 100%;
                font-size: 40px;
                margin-bottom: 30px;
            }
            .text-inslide{
                display: inline-block;
                width: 100%;
                h4{
                    color: #fff;
                    font-weight: 700;
                    margin-bottom: 0px;
                    display: block;
                }
                span{
                    color: #fff;
                    opacity: 0.6;
                    display: block;
                }
            }
        }
        .owl-dots{
            margin-top: 30px;
            text-align: center;
            font-size: 30px;
            button{
                width: 10px;
                height: 10px;
