/* portfolio detail */
.cm-portfolio{
    .content-modal-middle{
        .list-portfolio-images{
            margin: 0 -7.5px;
            padding-bottom: 30px;
            figure.snip1158 {
                float: left;
                position: relative;
                width: 50%;
                padding: 0 7.5px;
                span {
                    position: relative;
                    display: block;
                    overflow: hidden;
                    &:after {
                        background: #000000;
                        width: 0;
                        height: 6px;
                        position: absolute;
                        left: 0;
                        bottom: 0;
                        content: '';
                        -webkit-transition: all 0.3s ease-in-out;
                        transition: all 0.3s ease-in-out;
                    }
                }
                img {
                    opacity: 0.7;
                    max-width: 100%;
                    .transition();
                }
                figcaption {
                    position: absolute;
                    top: 50%;
                    left: 0;
                    width: 100%;
                    padding: 0 30px;
                    -webkit-transform: translateY(-50%);
                    transform: translateY(-50%);
                }
                h2,span {
                    margin: 0;
                    width: 100%;
                }
                h2 {
                    font-weight: 400;
                    text-transform: uppercase;
                }
                span {
                    color: #e6e6e6;
                    font-weight: 700;
                    text-transform: uppercase;
                }
                a {
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    z-index: 1;
                }
                &.white span:after {
                    background-color: #fff;
                }
                &.white span {
                    color: #fff;
                }
                &:hover img{
                    -webkit-transform: scale(1.2);
                    transform: scale(1.2);
                    opacity: 1;
                }
                &.hover img {
                    -webkit-transform: scale(1.2);
                    transform: scale(1.2);
                    opacity: 1;
                }
                &:hover span:after{
                    width: 100%;
                }
                &.hover span:after {
                    width: 100%;
                }
            }
        }
    }
    .content-modal-footer{
        height: 80px !important;
        bottom: 0px;
        padding-top: 30px;
        .cmfp-left{
            width: 50%;
            float: left;
            text-align: left;
            color: #fff;
            opacity: .5;
        }
        .cmfp-right{
            width: 50%;
            float: right;
            text-align: right;
            color: #fff;
            text-transform: uppercase;
            font-weight: 700;
            span{
                opacity: .4;
            }
            a{
                &:before{
                    content: " - ";
                    padding: 0px 5px;
                }
            }
        }
    }
}
.popup-detail-portfolio{
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 12;
    .closer-popup-port{
        display: block;
        position: absolute;
        top: 10px;
        right: 20px;
        z-index: 9;
        width: 30px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        color: #fff;
        opacity: 0.5;
        cursor: pointer;
        .transition();
    }
    .pdp-wrap{
        position: absolute;
        top: 50%;
        left: 50%;
        width: calc(~"100% - 30px");
        height: calc(~"100% - 90px");
        overflow-x: hidden;
        overflow-y: auto;
        transform: translate(-50%, -50%);
        z-index: 13;
        padding: 15px;
        border-radius: 3px;
        background-color: #fff;
        background: url("../images/topography.svg") #fff repeat top left;
        background-size: 30%;
        .pdp-wrap-left{
            float: left;
            width: 100%;
            overflow-x: auto;
            overflow-y: hidden;
            ul{
                list-style: none;
                width: max-content;
                li{
                    float: left;
                    text-align: center;
                    margin-right: 15px;
                    &:last-child{
                        margin-right: 0;
                    }
                    img{
                        max-width: none;
                        max-height: 180px;
                    }
                }
            }
        }
        .pdp-wrap-right{
            float: left;
            padding: 0 30px;
            margin: 30px 0;
            .pdpwr-title{
                font-size: 24px;
                font-weight: 700;
                font-style: normal;
            }
            .pdpwr-content{
                .links{
                    margin-top: 20px;
                    display: inline-block;
                    position: relative;
                    opacity: .6;
                    &:before{
                        content: "";
                        width: 100%;
                        height: 1px;
                        border-bottom: 1px dotted #ccc;
                        position: absolute;
                        bottom: 0px;
                        left: 0px;
                    }
                    &:hover{
                        opacity: 1;
                    }
                }
            }
        }
    }
    .dpd-overlay{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,.6);
    }
}
