/*-------------------
5. RESPONSIVE
---------------------*/
#bars{
    position: absolute;
    top: 0;
    right: -80px;
    width: 80px;
    height: 80px;
    background-color: #030408;
    cursor: pointer;
    transition: all .8s ease-in-out;
    &::before, &::after, .bar{
        display: block;
        position: absolute;
        left: 50%;
        height: 1px;
        width: 25px;
        margin-left: -12.5px;
        background-color: #fff;
        opacity: 1;
        transition: .25s ease-in-out;
        transform: rotate(0deg);
    }
    &::before{
        content: "";
        top: 30px;
    }
    &::after{
        content: "";
        top: 48px;
    }
    .bar{
        top: 50%;
        margin-top: -1.5px;
    }
    &.on{
        right: 0;
        .bar{
            left: 100%;
            visibility: hidden;
            opacity: 0;
        }
        &::before{
            top: 50%;
            transform: translateY(-50%) rotate(-45deg);
        }
        &::after{
            top: 50%;
            transform: translateY(-50%) rotate(45deg);
        }
    }
}
