/*-------------------------------------------------------------------/*
Template name : PerStar
Version    : 1.0.0
Author     : xdesky
Last change: 2018/05/06
/*-------------------------------------------------------------------
[Color codes]
    Background:   #030408
    Body Color :  #666666
    Accent color: #030408
[Typography]
    Body:           14px Roboto, sans-serif;
    Heading:        Poppins, sans-serif;
    Small Heading:  Playfair Display, serif;

[Table of contents]

    1. COMMON STYLES
    2. LEFT SIDEBAR
    3. VCARD
    4. CONTENT
        4.1. Common
    	4.2. Home
    	4.3. About
    	4.4. Resume
    	4.5. Skill
    	4.6. Portfolio
    	4.7. Contact
    5. RESPONSIVE

-------------------------------------------------------------------*/
/*-------------------
1. COMMON STYLES
---------------------*/
body {
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #333;
  background-color: #030408;
  font-weight: 500;
  -webkit-font-smoothing: antialiased;
}
img {
  max-width: 100%;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.3em;
  margin-top: 0;
  color: #222;
}
h1 {
  font-size: 36px;
}
h2 {
  font-size: 30px;
}
h3 {
  font-size: 24px;
}
h4 {
  font-size: 20px;
}
h5 {
  font-size: 16px;
}
h6 {
  font-size: 14px;
}
button:focus {
  outline: none;
}
a {
  color: #ffffff;
  text-decoration: none;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.transition {
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
a:hover,
a:focus {
  text-decoration: none;
  color: inherit;
  outline: none;
}
p {
  margin-bottom: 22px;
}
ul,
ol {
  margin: 0;
  padding: 0;
}
ul ul,
ol ol,
ul ol,
ol ul {
  padding-left: 30px;
}
.wrap-text {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: 300px;
  color: #666666;
}
.wrap-text a {
  color: #666666;
}
.pre-header {
  font-family: 'Playfair Display', serif;
  font-size: 20px;
}
.line {
  width: 40px;
  height: 5px;
  background-color: #fff;
  display: block;
  margin-bottom: 30px;
}
.mt20 {
  margin-top: 20px;
}
.mt30 {
  margin-top: 30px;
}
.grey {
  background-color: #ccc;
}
::-webkit-input-placeholder {
  color: inherit;
  opacity: 1;
  filter: alpha(opacity=100);
}
:-moz-placeholder {
  /* Firefox 18- */
  color: inherit;
  opacity: 1;
  filter: alpha(opacity=100);
}
::-moz-placeholder {
  /* Firefox 19+ */
  color: inherit;
  opacity: 1;
  filter: alpha(opacity=100);
}
:-ms-input-placeholder {
  color: inherit;
  opacity: 1;
  filter: alpha(opacity=100);
}
::-webkit-scrollbar {
  width: 10px;
  background-color: #F5F5F5;
}
::-webkit-scrollbar:horizontal {
  height: 10px;
}
::-webkit-scrollbar-thumb {
  background-color: #030408;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.5);
  background-color: #F5F5F5;
}
.body-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.75);
  opacity: 0;
  visibility: hidden;
  transition: 0.25s all ease-in-out;
}
.body-overlay.open {
  opacity: 1;
  visibility: visible;
}
.list-social {
  list-style: none;
  width: 100%;
}
.list-social li {
  display: inline-block;
  width: 35px;
  height: 35px;
  line-height: 37px;
  text-align: center;
  border: 2px solid #cecece;
  margin-right: 5px;
  opacity: 0.7;
  transition: all 0.25s ease-in-out;
  cursor: pointer;
}
.list-social li a {
  font-size: 0px;
  color: #cecece;
}
.list-social li .ps {
  font-size: 18px;
}
.list-social li:hover {
  border-color: #030408;
  opacity: 1;
}
.list-social li:hover a {
  color: #030408;
}
#particles-js {
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  z-index: 1;
}
/*-------------------
2. LEFT SIDEBAR
---------------------*/
.sidebar-left {
  position: fixed;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  padding-top: 80px;
  background-color: #030408;
  z-index: 9;
  transition: all 0.5s ease-in-out;
}
.sidebar-left.on {
  left: 0;
  width: 100%;
}
.sidebar-left .nav {
  display: block;
  flex-wrap: initial;
  height: 100%;
  overflow: hidden;
  overflow-y: auto;
}
.sidebar-left .nav li {
  display: table;
  float: left;
  width: 50%;
  height: 80px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.sidebar-left .nav li a {
  display: table-cell;
  vertical-align: middle;
  height: 100%;
  text-transform: uppercase;
  font-size: 11px;
  text-align: center;
}
.sidebar-left .nav li a .ps {
  font-size: 18px;
}
.sidebar-left .nav li a span {
  display: block;
}
.sidebar-left .nav li a.active {
  background-color: rgba(255, 255, 255, 0.1);
}
.sidebar-left .nav li a:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: #fff;
}
.sidebar-left .nav li:first-child span {
  display: none;
}
/*-------------------
3. VCARD
---------------------*/
.vcard-box {
  width: 350px;
  height: 100%;
  overflow-y: auto;
  position: fixed;
  z-index: 8;
  top: 0px;
  padding: 0px 30px;
  left: 80px;
  background: url("../images/topography.svg") #fff repeat top left;
}
.vcard-box .avatar-section {
  width: 168px;
  height: 168px;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 50px;
}
.vcard-box .avatar-section img {
  width: 100%;
}
.vcard-box .infor-section {
  width: 100%;
  text-align: center;
}
.vcard-box .infor-section .name-title {
  font-size: 40px;
  color: #201f24;
  font-weight: 700;
}
.vcard-box .infor-section .sub-title {
  font-size: 28px;
  color: #999999;
}
.vcard-box .infor-section .description {
  margin-top: 50px;
  text-align: justify;
  -moz-text-align-last: center;
  text-align-last: center;
  display: block;
}
.vcard-box .infor-section .btn-downloadcv {
  background-color: #201f24;
  display: inline-block;
  padding: 10px 20px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 12px;
  margin-top: 50px;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.vcard-box .infor-section .btn-downloadcv .ps {
  font-size: 16px;
  margin-right: 7px;
  vertical-align: -2px;
}
.vcard-box .infor-section .btn-downloadcv:hover {
  opacity: 0.8;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.vcard-box .infor-section .btn-downloadcv a:hover {
  color: #fff;
}
.vcard-box .footer-section {
  border-top: 1px dotted #b9b9b9;
  text-align: center;
  display: block;
  margin-top: 50px;
}
.vcard-box .footer-section p {
  margin-top: 30px;
  color: #666666;
}
/*-------------------
4. CONTENT
---------------------*/
/*
4.1. Common
*/
.content-wrap {
  position: relative;
  background-color: #030408;
}
.content-wrap .background-video {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
}
.content-wrap .content-flex {
  position: relative;
  height: 100vh;
}
.content-wrap .content-flex .regtangle {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: calc(0px +  580px*2/5);
  transform: translate(-50%, -50%);
  z-index: 4;
  display: block;
  border: 15px solid rgba(255, 255, 255, 0.2);
}
.content-wrap .c-common {
  position: relative;
  padding: 0 30px 50px;
}
.content-wrap .c-common:not(.sub-page) {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
  padding: 0;
}
.content-wrap .c-common:not(.sub-page) .c-common-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  padding: 0 30px;
  z-index: 4;
}
.content-wrap .c-common:not(.sub-page) .c-common-text h1 {
  font-size: 60px;
}
.content-wrap .c-common .c-common-text h1 {
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
}
.content-wrap .c-common .c-common-text h2 {
  color: #fff;
  font-size: 18px;
}
.content-wrap .c-common .c-common-text .c-common-des {
  color: #fff;
  font-size: 15px;
}
.content-wrap .c-common .c-common-text .action-btn {
  display: block;
  margin-top: 20px;
}
.content-wrap .c-common .c-common-text .action-btn a:hover {
  opacity: 0.4;
  color: #fff;
}
.content-wrap .c-common .c-common-text .action-btn .preview {
  width: 30px;
  display: inline-block;
  margin-right: 10px;
}
.content-wrap .c-common .c-common-text .action-btn .next {
  width: 30px;
  display: inline-block;
}
.content-wrap .text-free {
  font-size: 70px;
  letter-spacing: 5px;
  font-weight: 700;
  opacity: 0.1;
  text-transform: uppercase;
  position: fixed;
  width: 800px;
  height: 0px;
  line-height: 0px;
  z-index: 2;
  color: #fff;
  bottom: -25px;
  right: -750px;
  -ms-transform: rotate(-90deg);
  /* IE 9 */
  -webkit-transform: rotate(-90deg);
  /* Safari */
  transform: rotate(-90deg);
  -webkit-transform-origin: left top;
  -ms-transform-origin: left top;
  -moz-transform-origin: left top;
  -o-transform-origin: left top;
  transform-origin: left top;
  text-align: center;
}
.content-wrap .background {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
}
.content-wrap .background::after {
  content: '';
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
}
/*
4.3. About
*/
.sub-page h1 {
  font-size: 30px !important;
}
.title-pro {
  position: absolute;
  font-size: 50px;
  font-weight: 700;
  color: #fff;
  width: 100%;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
}
.title-pro::after {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 5px;
  margin-top: 5px;
  background-color: #fff;
}
.list-count {
  width: 100%;
  list-style: none;
  padding: 0 30px 50px;
  z-index: 5;
  text-align: center;
}
.list-count li {
  float: left;
  width: 50%;
  padding: 10px 0 10px 20px;
  position: relative;
  text-align: left;
  opacity: 0.5;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.list-count li h4 {
  position: relative;
  color: #fff;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 0px;
}
.list-count li h4::before {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -20px;
  content: "";
  width: 10px;
  height: 10px;
  background-color: #fff;
  border-radius: 10px;
}
.list-count li span {
  color: #fff;
  opacity: 0.6;
}
.list-count li:hover {
  opacity: 1;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
/*
4.4. Resume
*/
.resume-contents .content-modal {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  right: 0px;
  z-index: 5;
  background-color: #030408;
  overflow-x: hidden;
  overflow-y: auto;
  display: none;
}
.resume-contents .content-modal .closer-resume {
  display: block;
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 9;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  color: #fff;
  opacity: 0.5;
  cursor: pointer;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.resume-contents .content-modal .closer-resume:hover {
  -ms-transform: rotate(45deg);
  /* IE 9 */
  -webkit-transform: rotate(45deg);
  /* Safari */
  transform: rotate(45deg);
  opacity: 1;
}
.resume-contents .content-modal .resume-modal {
  width: 100%;
}
.resume-contents .content-modal .owl-nav {
  width: 100px;
  float: left;
  text-align: right;
  position: absolute;
  height: 50px;
  top: 70px;
  right: 60px;
}
.resume-contents .content-modal .owl-nav button {
  width: 30px;
  height: 30px;
  line-height: 30px;
  display: inline-block;
  margin-top: 18px;
  color: #fff;
  font-size: 0;
  text-align: center;
  cursor: pointer;
  opacity: 0.5;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.resume-contents .content-modal .owl-nav button:hover {
  opacity: 1;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.resume-contents .content-modal .owl-nav button::before {
  content: "";
  font-family: 'perstar' !important;
  speak: none;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.resume-contents .content-modal .owl-nav .disabled {
  opacity: 0.1;
}
.resume-contents .content-modal .owl-nav .owl-prev::before {
  content: "\e90c";
}
.resume-contents .content-modal .owl-nav .owl-next::before {
  content: "\e911";
}
.resume-contents .content-modal .secondary:hover,
.resume-contents .content-modal .secondary:focus,
.resume-contents .content-modal .secondary:hover,
.resume-contents .content-modal .secondary:focus {
  color: #fff;
  opacity: 1;
}
.resume-contents .content-modal .cm-wraper {
  width: 100%;
  display: block;
  padding: 65px 30px 0 30px;
  z-index: 10;
  position: relative;
  float: left;
}
.resume-contents .content-modal .cm-wraper .content-modal-header {
  width: 100%;
  height: 100px;
  margin-top: 20px;
  display: block;
  float: left;
}
.resume-contents .content-modal .cm-wraper .content-modal-header .cmh-left {
  width: 100%;
  float: left;
  padding-left: 50px;
  color: #fff;
}
.resume-contents .content-modal .cm-wraper .content-modal-header .cmh-left .cmhl-sub-title {
  display: block;
  position: relative;
  font-family: 'Playfair Display', serif;
  font-size: 18px;
  opacity: 0.5;
  width: calc(100% - 90px);
}
.resume-contents .content-modal .cm-wraper .content-modal-header .cmh-left .cmhl-sub-title::before {
  content: "";
  width: 40px;
  height: 4px;
  background-color: #fff;
  position: absolute;
  left: -50px;
  top: 15px;
}
.resume-contents .content-modal .cm-wraper .content-modal-header .cmh-left .cmhl-main-title {
  display: block;
  font-size: 25px;
  font-weight: 700;
  margin-top: 15px;
  text-transform: uppercase;
}
.resume-contents .content-modal .cm-wraper .content-modal-header .cmh-btn-slider {
  float: left;
  width: 100px;
  text-align: right;
}
.resume-contents .content-modal .cm-wraper .content-modal-header .cmh-btn-slider .prev {
  width: 30px;
  height: 30px;
  display: inline-block;
  background: url('../images/icons/left-arrow.svg') center center no-repeat;
  background-size: 50%;
  font-size: 0px;
  margin-top: 18px;
  cursor: pointer;
  opacity: 0.3;
}
.resume-contents .content-modal .cm-wraper .content-modal-header .cmh-btn-slider .next {
  width: 30px;
  height: 30px;
  display: inline-block;
  background: url('../images/icons/right-arrow.svg') center center no-repeat;
  background-size: 50%;
  font-size: 0px;
  margin-top: 18px;
  cursor: pointer;
  opacity: 0.3;
}
.resume-contents .content-modal .cm-wraper .content-modal-header .cmh-textbg {
  width: 100%;
  font-size: 45px;
  font-weight: 700;
  color: #fff;
  opacity: 0.06;
  position: fixed;
  top: 92px;
  right: 0;
  text-transform: uppercase;
  text-align: right;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle {
  width: 100%;
  padding-top: 30px;
  display: block;
  float: left;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle .cmm-image {
  float: left;
  position: relative;
  width: 220px;
  height: 283px;
  margin-bottom: 80px;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle .cmm-image img {
  width: 100%;
  z-index: 11;
  position: absolute;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle .cmm-image .border-img {
  width: 220px;
  height: 283px;
  position: absolute;
  top: 50px;
  right: -50px;
  border: 10px solid #fff;
  opacity: 0.1;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle .cmm-contents {
  float: left;
  color: #fff;
  padding-bottom: 50px;
  font-size: 16px;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle .cmm-contents .link-web {
  display: block;
  opacity: 0.5;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle .cmm-contents .link-web::before {
  content: "\e915";
  font-family: 'perstar' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-right: 10px;
  vertical-align: middle;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle .cmm-contents .wrap-text {
  color: #fff;
  border-left: 3px solid rgba(255, 255, 255, 0.3);
  padding-left: 15px;
  font-size: 16px;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle .cmm-contents ul {
  list-style: none;
  margin-left: 15px;
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
  opacity: 0.7;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle .cmm-contents ul li {
  line-height: 34px;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle .cmm-pretext {
  font-size: 200px;
  font-weight: 700;
  color: #fff;
  position: absolute;
  right: 50px;
  bottom: 180px;
  opacity: 0.1;
}
.resume-contents .content-modal .cm-wraper .content-modal-middle .cmm-pretext:before {
  content: "0";
  font-size: 200px;
  font-weight: 700;
  color: #fff;
}
.resume-contents .content-modal .cm-wraper .content-modal-footer {
  width: 100%;
  height: 110px;
  display: block;
  float: left;
  margin-bottom: 30px;
}
.resume-contents .content-modal .cm-wraper .content-modal-footer .years-resume li {
  opacity: 0.3;
}
.resume-contents .content-modal .cm-wraper .content-modal-footer .years-resume li.active {
  opacity: 1;
}
.resume-contents .content-modal .cm-wraper .content-modal-footer .years-resume li:hover {
  opacity: 1;
}
.resume-contents .content-modal .cm-overlay {
  background-color: rgba(0, 0, 0, 0.75);
  width: 100%;
  height: 100%;
  z-index: 4;
  position: absolute;
}
.resume-contents .content-modal .cm-background {
  width: 100%;
  height: 100%;
  z-index: 2;
  position: absolute;
  background-position: center;
  background-size: cover;
}
/*
4.5. Skill
*/
.skills-container {
  position: relative;
  padding: 0 30px 50px;
}
.skills-container .bar {
  width: 100%;
  margin: 4px 0px;
  float: left;
}
.skills-container .bar .skill {
  font-size: 0.8em;
  text-transform: uppercase;
  margin-bottom: 0.25em;
  color: #fff;
  float: left;
  width: 30%;
}
.skills-container .bar .bar-outer {
  height: 5px;
