/*-------------------
5. RESPONSIVE
---------------------*/
#bars {
  position: absolute;
  top: 0;
  right: -80px;
  width: 80px;
  height: 80px;
  background-color: #030408;
  cursor: pointer;
  transition: all 0.8s ease-in-out;
}
#bars::before,
#bars::after,
#bars .bar {
  display: block;
  position: absolute;
  left: 50%;
  height: 1px;
  width: 25px;
  margin-left: -12.5px;
  background-color: #fff;
  opacity: 1;
  transition: 0.25s ease-in-out;
  transform: rotate(0deg);
}
#bars::before {
  content: "";
  top: 30px;
}
#bars::after {
  content: "";
  top: 48px;
}
#bars .bar {
  top: 50%;
  margin-top: -1.5px;
}
#bars.on {
  right: 0;
}
#bars.on .bar {
  left: 100%;
  visibility: hidden;
  opacity: 0;
}
#bars.on::before {
  top: 50%;
  transform: translateY(-50%) rotate(-45deg);
}
#bars.on::after {
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
}
